<template>
  <div>
    <van-nav-bar
      :fixed="true"
      title="设备故障"
      @click-left="()=>{
          $router.go(-1)
         }"
      left-arrow
      class="backgroundNaviBar"
      z-index="999"
      :border="false"
    >
      <!-- <van-icon name="filter-o" slot="right" size="20" /> -->
    </van-nav-bar>
    <div class="back">
      <div class="content border-radius" v-for="(item,index) in itemListContent" :key="index">
        <div class="title">
          <span>站点名称</span>
          <span>{{item.stationName}}</span>
        </div>
        <div class="tableContent">
          <div class="table">
            <span>地址</span>
            <span>{{item.address}}</span>
          </div>
          <div class="table">
            <span>设备名称</span>
            <span>{{item.deviceName}}</span>
          </div>
          <div class="table">
            <span>运维人</span>
            <span>{{item.operator}}</span>
          </div>
          <div class="table">
            <span>联系电话</span>
            <span style="color:#126cf9" @click="callTo(item.phone)">{{item.phone}}</span>
          </div>
          <div class="textContent">
            <div class="table1">
              <span>故障内容</span>
            </div>
            <div>
              <textarea :value="item.reason" name id="textarea"></textarea>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import AppBace from "../../../static/ajax";
import { Dialog } from "vant";

export default {
  data() {
    return {
      itemListContent: []
    };
  },
  async created() {
    var token = sessionStorage.getItem("token");
    var url = "/station/water-station/faultDevices";
    var res = await AppBace.request(url, "GET", {}, token);
    console.log("超标站点", res);
    this.itemListContent = res.data.data;
  },
  methods: {
    callTo(number) {
      Dialog.confirm({
        message: number,
        className: "dialogContent"
      })
        .then(() => {
          // on confirm
          window.location.href = "tel://" + number;
        })
        .catch(() => {
          // on cancel
        });
    }
  }
};
</script>
<style scoped>
.back {
  padding: 10px;
  margin-top: 46px;
}
.content {
  background-color: white;
  padding: 10px;
  /* border-radius: 10px; */
  margin-bottom: 10px;
}
.header {
  position: fixed;
  top: 0;
}
.title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 40px;
  border-bottom: 1px solid rgb(240, 240, 240);
  font-weight: 600;
}
.tableContent {
  padding: 10px;
}
.table {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 40px;
  border-bottom: 1px rgb(240, 240, 240) solid;
  font-size: 12px;
  padding: 0 16px;
  color: rgba(0, 0, 0, 0.87);
}
#textarea {
  width: 100%;
  height: 100px;
  border-radius: 10px;
  padding: 10px;
}
.textContent {
  padding: 0 16px;
  font-size: 12px;
  color: rgba(0, 0, 0, 0.87);
  /* padding: 10px; */
}
.table1 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 40px;
  border-bottom: 1px rgb(240, 240, 240) solid;
  font-size: 12px;
  /* padding: 0 16px; */
  color: rgba(0, 0, 0, 0.87);
}
</style>


